import React, { Fragment } from 'react';
import { graphql } from 'gatsby';

import Hero from '../components/Hero';
import Social from '../components/Social';
import SEO from '../components/Seo';

const IndexPage = ({ location, data }) => {
  const { avatar, contentfulPage, contentfulWebsite } = data;
  const { metadata } = contentfulPage;

  return (
    <Fragment>
      <SEO title={metadata.title} keywords={[metadata.keywords]} />
      <Hero avatar={avatar} content={contentfulPage.hero} />
      <Social links={contentfulWebsite.metadata.socialLinks} />
    </Fragment>
  );
};

export default IndexPage;

export const pageQuery = graphql`
  query {
    contentfulPage(name: { eq: "Home Page" }) {
      hero {
        titlePrefix
        title
        description
      }
      metadata {
        title
        keywords
      }
    }
    contentfulWebsite(name: { eq: "Site" }) {
      metadata {
        socialLinks {
          label
          url
        }
      }
    }
    avatar: file(absolutePath: { regex: "/guy-profile-pic.jpg/" }) {
      childImageSharp {
        fixed(width: 150, height: 150) {
          ...GatsbyImageSharpFixed
        }
      }
    }
  }
`;
